import axios from 'axios'
import ImageCarousel from '../../../components/ImageCarousel'

async function getProduct(slug) {
    const BASE_URL = 'http://localhost:1337'
    const res = await axios.get(`${BASE_URL}/api/products?populate=*&filters[slug][$eq]=${slug}`)
    return res.data.data[0]
}

export default async function ProductDetail({ params }) {
    const BASE_URL = "http://localhost:1337"
    const slug = params?.slug
    const product = await getProduct(slug)

    const images = product?.images?.map((image) => (
        BASE_URL + image?.formats?.small?.url
    ))

    const availableSizes = product?.sizes || []
    const allSizes = [39, 40, 41, 42, 43, 44, 45, 46]

    return (
        <div className="p-4 max-w-4xl mx-auto">
            {/* Show only on small screens */}
            <div className="block md:hidden mb-4">
                <ImageCarousel images={images} />
            </div>

            <h1 className="text-2xl font-bold">{product.title}</h1>
            <p className="text-gray-600 line-through">Rs. {product.oldPrice}</p>
            <p className="text-red-600 font-bold text-xl">Rs. {product.price}</p>

            {/* Sizes */}
            <div className="flex flex-wrap gap-2 mt-4">
                {allSizes.map((size) => {
                    const isAvailable = availableSizes.includes(size)
                    return (
                        <button
                            key={size}
                            disabled={!isAvailable}
                            className={`
                                px-4 py-2 border text-sm rounded 
                                ${isAvailable ? 'bg-white text-black hover:border-black' : 'opacity-40 line-through cursor-not-allowed'}
                            `}
                        >
                            {size}
                        </button>
                    )
                })}
            </div>

            {/* Add to Bag */}
            <button className="w-full bg-black text-white py-3 text-center font-semibold mt-6 hover:opacity-90 transition">
                ADD TO BAG
            </button>

            {/* Description */}
            <div className="mt-6">
                <h2 className="text-lg font-semibold mb-2">Product Description</h2>
                <p className="text-gray-700 whitespace-pre-line">{product.description}</p>
            </div>
        </div>
    )
}
